.hiking{
    height: auto;
    background-color: #000;
    padding-bottom: 10%;
}

.hik{
    width: 120%;
    height: 100%;
}

.hikingHeading{
    text-align: center;
    color: #FFDC27;
    text-transform: uppercase;
    padding-top: 3rem;
    font-weight: 300;
}


.hikerTwo{
    color: #fff;
    margin: 39% 0 10% 1.5rem;
    position: absolute;
    font-weight: bolder;

}

.hikerParagraphHeader{
    text-align: left;
    font-weight: bolder;
    color: #fff;
    opacity: 0;
}

.hikerParagraph{
    text-align: left;
    color: #fff;
    font-size: 12px;
    position: relative;
    opacity: 0;
}

.hikerButton{
    width: 10rem;
    background-color: transparent;
    color: #FFDC27;
    border: 1px solid #FFDC27;
    height: 3rem;
    margin: 3% 70% 0 0 !important;
}

.allin{
    position: relative;
}

.allin::after{
    content: " ";
    position: absolute;
    height: 25%;
    opacity: 0;
}

.writing{
    margin-top: 15%;
}

@media (min-width:300px) and (max-width: 900px){
    .hikerParagraphHeader{
        margin: 5rem 2rem 2rem 2rem !important;
        text-align: left;
    }

    .hikerParagraph{
        margin: 0 2rem;
    }

    .hikerTwo {
        color: #fff;
        margin: 33% 0 10% 4.5rem !important;
        position: absolute;
        font-weight: bolder;
    }

    .hikerButton{
        width: 10rem;
        background-color: transparent;
        color: #FFDC27;
        border: 1px solid #FFDC27;
        height: 3rem;
        margin: 5% 26% 0 0 !important;
    }
    
    .writing{
        margin-top: 0% !important;
    }
}