

.hik{
    width: 100%;
    height: 100%;
}


.trainingParagraphHeader{
    z-index: -100;
}

.Head {
    position: relative;
    height: auto;
    background-image:url('../images/bac.png');
    padding: 4.236em 0 2.618em;
    background-repeat: no-repeat;
    background-size: cover;
  }

  
  .Head:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background:rgb(0,0,0);
    background: transparent\9;
    background:rgba(0,0,0,0.9);        
  }

  .trainingParagraphHeader{
      text-align: left;
      color: #fff;
  }

  .yellow{
      color: #FFDC27 !important;
      background-color: transparent;
  }

  .trainingParagraph{
      text-align: left;
      font-size: 13px;
  }