
.Had {
    position: relative;
    height: 100%;
    background-image:url('../images/swim.png') !important;
    padding: 12.236em 0 2.618em;
    background-repeat: no-repeat;
    background-size: cover;
  }

  
  .Had:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background:rgb(0,0,0);
    background: transparent\9;
    background:rgba(0,0,0,0.8);        
  }

  .climb {
    position: relative;
    height: 100%;
    background-image:url('../images/climb.png') !important;
    padding: 12.236em 0 2.618em;
    background-repeat: no-repeat;
    background-size: cover;
  }

  
  .climb:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background:rgb(0,0,0);
    background: transparent\9;
    background:rgba(0,0,0,0.8);        
  }

.startButton{
