.gliding{
    height: auto;
    background-color: #000;
    padding-bottom: 10%;
}

.hik{
    width: 120%;
    height: 100%;
}

.glidingHeading{
    text-align: center;
    color: #FFDC27;
    text-transform: uppercase;
    padding-top: 3rem;
    font-weight: 300;
}


.gliderTwo{
    color: #fff;
    margin: 39% 0 10% 1.5rem;
    position: absolute;
    font-weight: bolder;

}

.gliderParagraphHeader{
    text-align: right;
    font-weight: bolder;
    color: #fff;
}

.gliderParagraph{
    text-align: right;
    color: #fff;
    font-size: 12px;
}

.gliderButton{
    width: 10rem;
    background-color: transparent;
    color: #FFDC27;
    border: 1px solid #FFDC27;
    height: 3rem;
    margin: 3% 72% 0 0 !important;
}

.writing{
    margin-top: 20%;
}

@media (min-width:300px) and (max-width: 900px){
    .gliderParagraphHeader{
        margin: 5rem 2rem 2rem 2rem !important;
        text-align: right;
    }

    .gliderParagraph{
        margin: 0 2rem;
    }

    .gliderTwo {
        color: #fff;
        margin: 33% 0 10% 4.5rem !important;
        position: absolute;
        font-weight: bolder;
    }

    .gliderButton{
        width: 10rem;
        background-color: transparent;
        color: #FFDC27;
        border: 1px solid #FFDC27;
        height: 3rem;
        margin: 5% 26% 0 0 !important;
    }
    
    .writing{
        margin-top: 0% !important;
    }
}