.hunting{
    height: auto;
    background-color: #000;
    padding-bottom: 10%;
}

.huntingHeading{
    text-align: center;
    color: #FFDC27;
    text-transform: uppercase;
    padding-top: 3rem;
    font-weight: 300;
}


.hunterTwo{
    color: #fff;
    margin: 39% 0 10% -1rem;
    position: absolute;
    font-weight: bolder;

}

.hunterParagraphHeader{
    text-align: left;
    font-weight: bolder;
    color: #fff;
}

.hunterParagraph{
    text-align: left;
    color: #fff;
    font-size: 12px;
}

.hunterButton{
    width: 10rem;
    background-color: transparent;
    color: #FFDC27;
    border: 1px solid #FFDC27;
    height: 3rem;
    margin: 3% 70% 0 0 !important;
}

.writing{
    margin-top: 24%;
}

@media (min-width:300px) and (max-width: 900px){
    .hunterParagraphHeader{
        margin: 5rem 2rem 2rem 2rem !important;
        text-align: left;
    }

    .hunterParagraph{
        margin: 0 2rem;
    }

    .hunterTwo {
        color: #fff;
        margin: 33% 0 10% 4.5rem !important;
        position: absolute;
        font-weight: bolder;
    }

    .hunterButton{
        width: 10rem;
        background-color: transparent;
        color: #FFDC27;
        border: 1px solid #FFDC27;
        height: 3rem;
        margin: 5% 26% 0 0 !important;
    }
    
    .writing{
        margin-top: 0% !important;
    }
}